import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { VocabEvaluateComponent } from './vocab-evaluate/vocab-evaluate.component';
import { RouterModule, Routes } from '@angular/router';
import { VocabDetailResolver } from './vocab-evaluate.reslover';
import { BasicModule } from '@app/shared/basic-components/basic.module';

const routes: Routes = [

  {
    path: 'vocabulary/:qcode/:questionId',
    component: VocabEvaluateComponent,
    resolve: {
      evaluateData: VocabDetailResolver
    }
  }
]

@NgModule({
  declarations: [
    VocabEvaluateComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
  ],
  providers:[
    VocabDetailResolver
  ]
})
export class TrainingEvaluateModule { }
