import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { DetailService } from '@app/core/http/detail.service';
import { PredictionDetail } from '@app/shared/models/prediction';
import { Vocab } from '@app/shared/models/vocab';

@Injectable()
export class VocabDetailResolver implements Resolve<PredictionDetail> {

    constructor(private question: DetailService) {
    }

    resolve(route: ActivatedRouteSnapshot):
        Observable<any> {

        return this.question.getEvaluateVocab(route.params['qcode'],route.params['questionId']);
    }
}