import { Component, ElementRef, HostListener, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CommonService } from '@app/core/http/common.service';
import { SaveService } from '@app/core/http/save.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-vocab-evaluate',
  templateUrl: './vocab-evaluate.component.html',
  styleUrls: ['./vocab-evaluate.component.css']
})
export class VocabEvaluateComponent implements OnInit, OnDestroy {
  public vocabData: any;
  public moduleName: string;
  public optionIndex: any;
  public answers = [];
  public sub: Subscription;
  public result:any;


  constructor(
    private _activeRoute: ActivatedRoute,
    private _cservice: CommonService,
    private _save: SaveService
  ) { }


  ngOnInit() {
    this.vocabData = this._activeRoute.snapshot.data['evaluateData'];
    this.moduleName = this._cservice.getVocabName(this.vocabData.questionType);
    this.generateAnswerFormat();
  }


  generateAnswerFormat() {
    this.answers = new Array<any>(this.vocabData.questions.length - 1);
    if (this.vocabData.questionType == 'V006')
      this.answers.fill(new Array<any>(2).fill(''));
    else
      this.answers.fill('');

      console.log(this.answers)
  }

  getDataQ1(index, value) {
    console.log(index, value)
    this.answers[index] = value;
  }

  getQ6Ans(mainIndex, index, value) {
    console.log(this.answers[mainIndex].length)
    let ans = this.answers[mainIndex];
    ans[index] = value;
    this.answers[index] = ans;
  }

  onEvaluate() {
    let finalData = {
      "answers": this.answers,
      "questionId": this.vocabData.questionId,
      "questionType": this.vocabData.questionType,
      "questions":this.vocabData.questions
    }
    console.log(finalData)
    this.sub = this._save.onEvaluateVocab(finalData).subscribe
      ((res) => {
        this.result=res;
      })
  }

  ngOnDestroy(): void {
    if (this.sub) this.sub.unsubscribe();
  }


}
